@font-face {
    font-family: 'SpaceMission';
    src: url('../fonts/SpaceMission-rgyw9.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

html {
    margin: 0;
    padding: 0;
}

body {
    width: 100vw;
    height: auto;
    overflow-y: hidden;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.wdw {
    width: 100vw;
    min-height: 100vh;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-size: 1.1rem;
}

#wdw-webcam{
    display: flex;
    background-image: url('../img/milkyway.jpg');
    background-position: center;
    background-repeat: no-repeat;
}

#wdw-game{
    display: none;
    font-family: 'SpaceMission';
}

.txt-game{
    font-family: 'SpaceMission', sans-serif;
    font-size: 2em;
    color: #4deeea;
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
}

.txt-info{
    font-size: 100%;
    font-family: 'Roboto light', sans-serif;
    color: white;
    margin-bottom: 1em;
}
.theme-color{
    background-color: #4deeea !important;
    color: black !important;  
}

.margin-1em{
    margin-bottom: 1em;
}

